input[type=datetime]:not(.browser-default):focus:not([readonly]),
input[type=datetime-local]:not(.browser-default):focus:not([readonly]),
input[type=tel]:not(.browser-default):focus:not([readonly]),
input[type=number]:not(.browser-default):focus:not([readonly]),
input[type=search]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]) {
  box-shadow: none; }

.btn, .btn-large, .btn-small {
  color: #333333; }

.App {
  position: relative;
  background-color: #292C35;
  max-height: 100vh;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif; }

.block-navbar {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 1;
  border-bottom: 2px solid #FCCF3E; }
  .block-navbar nav {
    height: 56px;
    line-height: 56px; }
  .block-navbar .brand-logo {
    font-size: 1.5rem; }

.block-main {
  padding-top: 71px;
  padding-bottom: 61px;
  overflow-x: hidden;
  height: 100%;
  background-color: #292c35;
  background-repeat: no-repeat;
  background-origin: content-box;
  background-position: right 15px bottom 5px;
  background-image: url("./../images/wavbo.svg"); }
  .block-main .container {
    display: grid;
    grid-auto-columns: 100%;
    grid-auto-flow: column;
    transition: transform 0.5s; }
    .block-main .container > .col {
      padding: 0 15px; }

.block-footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  height: 92px;
  background-color: #f5f5f5;
  display: flex; }
  .block-footer .btn, .block-footer .btn-large, .block-footer .btn-small {
    background-color: #D7D7D7;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    height: 100%;
    font-size: 1.33rem;
    border-radius: 0; }
  .block-footer .progress {
    position: absolute;
    z-index: 2;
    margin: 0;
    bottom: 0; }

.input-field input {
  color: white; }

.frame {
  position: absolute;
  right: 0px;
  left: 0px;
  top: 0;
  border: 6px solid #d7d7d7;
  padding-top: 10px;
  height: 100vh;
  max-height: 100vh;
  overflow: hidden; }
  .frame .frame-img {
    width: 100%;
    display: block; }

.wavbo-rip {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center; }
  .wavbo-rip .wavbo-rip-img {
    max-width: 70%;
    width: 70%;
    display: block; }
